import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    email: "",
    password: "",
    phone: "",
    users: [{
      email: 'j',
      password: 'j',
      phone: 'j',
      suspended: null
    }, {
      email: 'c',
      password: 'c',
      phone: 'c',
      suspended: null
    }]
  },
  getters: {
    getDetails: state => {
      return {
        email: state.email,
        password: state.password
      };
    }
  },
  mutations: {
    setEmail: (state, payload) => {
      state.email = payload
    },
    setPassword: (state, payload) => {
      state.password = payload
    },
    setPhone: (state, payload) => {
      state.phone = payload
    }
  },
  actions: {},
  modules: {}
})